<template>
    <div>
        <h3 class="margin-b-14">
            <ocr-button itemid="applicantBusinessLicence" ocrtype="2" @on-ocr-result="setOcrRes">
            </ocr-button>
            <ocr-button itemid="applicantOrderIdPositive" ocrtype="1" @on-ocr-result="setOcrRes">
                
