            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="" class="hascol-form-item">
                        <region-picker type="3" v-model="formModel.enterpriseRegistration_Region"
                                       regionlabel="" ref="regionRef1"
                                       @on-change="changeRegion('enterpriseRegistration','enterprise.enterpriseRegistration_Region')"></region-picker>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="16">
                    <FormItem label="" prop="enterpriseRegistrationAddress">
                        <Input clearable
                               v-model="formModel.enterpriseRegistrationAddress" placeholder=""
