                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="" prop="corporationCertificateType">
                        <i-select type="text" v-model="formModel.corporationCertificateType"
                                  placeholder=""
                                  @on-change="val=>{ handleChange(val,'enterprise.corporationCertificateType')}">
                            <i-option v-for="(item,index) in dp['frCertType']" :value="item.dictCode"
                                      :key="item.dictCode">
