                            </i-option>
                        </i-select>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="" prop="corporationCertificateNo" :rules="createCertNoRule()">
                        <Input clearable v-model="formModel.corporationCertificateNo" placeholder=""
                               v="enterprise.corporationCertificateNo"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="" prop="corporationPhone">
                        <Input clearable v-model="formModel.corporationPhone" placeholder=""
                               v="enterprise.corporationPhone"
                               @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="(%)" prop="corporationShareholdingRatio">
                        <InputNumber clearable
                                     v-model="formModel.corporationShareholdingRatio" placeholder=""
                                     v="enterprise.corporationShareholdingRatio"
                                     @on-blur="handleBlur" @on-clear="handleBlur"></InputNumber>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="" prop="enterpriseContactPhone">
                        <Input clearable v-model="formModel.enterpriseContactPhone" placeholder=""
