import {
    validators,
    required,
    pctValidator,
    amountValidator,
} from "@/utils/validateRules";

export default {
    //TODO :
    personalRule: {
        'custName': [validators.custName('')],
        'custGender': [required('')],
        'custPhone': [required(''), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        'custCertificateType': [required('')],
        'custCertificateNo': [required(''), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'marriage': [required('')],
        'custEducation': [required('')],
        'custDegree': [required('')],
        'hasEstate': [required('')],
        'livingStatus': [required('')],
        'industry': [required('')],
        'companyNature': [required('')],
        'companyName': [validators.headAndTailNotHasBlank('', true), {
            max: 100,
            message: '100',
            trigger: 'change'
        }],
        'custProfession': [required('')],
        'custPosition': [required('')],
        'custTitle': [required('')],
        'annualSalaryAfterTax': [amountValidator('', true, 1000)],

        'currentResidenceYears': [validators.yearsValidator('', true)],
        'postalCode': [validators.zipValidator('', true)],

        company_Region: [validators.regionValidator('', 3)],
        'companyProvince': [required('')],
        'companyCity': [required('')],
        'companyDistrict': [required('/')],
        'companyAddress': [validators.addressValidator('', true)],

        currentResidence_Region: [validators.regionValidator('', 3)],
        'currentResidenceProvince': [required('')],
        'currentResidenceCity': [required('')],
        'currentResidenceDistrict': [required('/')],
        'currentResidenceAddress': [validators.addressValidator('', true)],

        censusRegister_Region: [validators.regionValidator('', 3)],
        'censusRegisterProvince': [required('')],
        'censusRegisterCity': [required('')],
        'censusRegisterDistrict': [required('/')],
        'censusRegisterAddress': [validators.addressValidator('', true)],

    },
    //TODO : 
    enterpriseRule: {
        'orderApplicationType': [required('')],
        'unifiedSocialCreditCode': [required(''),
            {validator: validators.isCreditCode, trigger: 'change'}],
        'enterpriseName': [validators.headAndTailNotHasBlank('', true), {
            max: 100,
            message: '100',
            trigger: 'change'
        }],
        'country': [required('')],
        'isInstitution': [required('')],
        'registrationType': [required('')],
        'eigenCode': [],
        'organizationCode': [required(''),
            {validator: validators.checkOrgCodeValid, trigger: 'change'}],
        'taxpayerIdentificationNum': [required(''),
            {validator: validators.checkTaxpayerId, trigger: 'change'}],
        'establishDate': [
            {
                validator: (rule, value, callback) => {

                    let filed = '';
                    if (value == '') {
                        callback(new Error(filed + ''));
                    } else if (Object.prototype.toString.call(value) == '[object Date]' || typeof value == 'string') {
                        callback();
                    } else {
                        callback(new Error(filed + ''));
                    }
                },
                trigger: 'change'
            }
        ],
        'isLongCertificate': [required('')],
        'certificateExpiryDate': [],
        'organizationCategory': [required('')],
        'organizationCategoryDetail': [required('')],
        'enterpriseScale': [required('')],
        'registeredCapitalCurrency': [required('')],
        'registeredCapital': [
            amountValidator('', true)
        ],
        'organizationStatus': [required('')],
        'economicType': [required('')],
        'economicIndustryLargeClass': [required('')],
        'economicIndustrySmallClass': [required('')],
        'enterpriseBusinessScope': [required(''), {
            max: 1000,
            message: '1000',
            trigger: 'change'
        }],

        enterpriseRegistration_Region: [validators.regionValidator('', 3)],
        'enterpriseRegistrationProvince': [required('')],
        'enterpriseRegistrationCity': [required('')],
        'enterpriseRegistrationDistrict': [required('/')],
        'enterpriseRegistrationAddress': [validators.addressValidator('', true)],

        enterpriseManagement_Region: [validators.regionValidator('', 3)],
        'enterpriseManagementProvince': [required('')],
        'enterpriseManagementCity': [required('')],
        'enterpriseManagementDistrict': [required('/')],
        'enterpriseManagementAddress': [validators.addressValidator('', true)],

        'enterpriseContactPhone': [required(''), {
            validator: validators.phone,
            trigger: 'change'
        }],
        "corporationName": [validators.custName('')],
        "corporationCertificateType": [required('')],
        "corporationCertificateNo": [
            /*required(''), {
            validator: validators.certNo,
            trigger: 'change'
            }*/
        ],
        "corporationPhone": [required(''), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        "corporationShareholdingRatio": [
            pctValidator('', true)
        ],
    },
    //TODO : 
    relativeRule: {
        "relativeType": [required('')], //
        "relativeName": [validators.custName('')], //
        "relativeCertificateType": [required('')], //
        "relativeCertificateNo": [required(''), {
            validator: validators.certNo,
            trigger: 'change'
        }], //
        "relativePhone": [required(''), {
            validator: validators.mobile,
            trigger: 'change'
        }], //
        "relativeAnnualSalaryAfterTax": [amountValidator('', true, 1000)], //
        "relativeCompanyName": [validators.headAndTailNotHasBlank('', true), {
            max: 100,
            message: '100',
            trigger: 'change'
        }], //

        relativeCompany_Region: [validators.regionValidator('', 3)],
        "relativeCompanyProvince": [required('')], //
        "relativeCompanyCity": [required('')], //
        "relativeCompanyDistrict": [required('/')], //
        "relativeCompanyAddress": [validators.addressValidator('', true)], //
    },
    //TODO : 
    contactorRule: {
        'contactRelationship': [required('')],
        'contactName': [validators.custName('')],
        'contactPhone': [required(''), {
            validator: validators.mobile,
            trigger: 'change'
        }],
    },
    //TODO : 
    guarantorPersonalRule: {
        'guarantorRelationship': [required('')],
        'guarantorName': [validators.custName('')],
        'guarantorCredentialType': [required('')],
        'guarantorIdentityCard': [required(''), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'guarantorPhone': [required(''), {
            validator: validators.mobile,
            trigger: 'change'
        }],
        'companyName': [validators.headAndTailNotHasBlank('', true), {
            max: 100,
            message: '100',
            trigger: 'change'
        }],
        'companyPhone': [required(''), {
            validator: validators.phone,
            trigger: 'change'
        }],

        company_Region: [validators.regionValidator('', 3)],
        'companyProvince': [required('')],
        'companyCity': [required('')],
        'companyDistrict': [required('/')],
        'companyDetailAddress': [validators.addressValidator('', true)],

        currentResidence_Region: [validators.regionValidator('', 3)],
        'currentResidenceProvince': [required('')],
        'currentResidenceCity': [required('')],
        'currentResidenceDistrict': [required('/')],
        'currentResidenceAddress': [validators.addressValidator('', true)],

        censusRegister_Region: [validators.regionValidator('', 3)],
        'censusRegisterProvince': [required('')],
        'censusRegisterCity': [required('')],
        'censusRegisterDistrict': [required('/')],
        'censusRegisterAddress': [validators.addressValidator('', true)],
    },
    //TODO: 
    guarantorEnterpriseRule: {
        'guarantorRelationship': [required('')],
        'enterpriseName': [validators.headAndTailNotHasBlank('', true), {
            max: 100,
            message: '100',
            trigger: 'change'
        }],
        'enterpriseNature': [required('')],
        'unifiedSocialCreditCode': [required(''), {
            validator: validators.isCreditCode,
            trigger: 'change'
        }],

        'corporationName': [validators.custName('')],
        'corporationIdentityCard': [required(''), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'corporationPhone': [required(''), {
            validator: validators.phone,
            trigger: 'change'
        }],
        'actualControllerName': [validators.custName('')],
        'actualControllerIdentityCard': [required(''), {
            validator: validators.certNo,
            trigger: 'change'
        }],
        'actualControllerPhone': [required(''), {
            validator: validators.phone,
            trigger: 'change'
        }],

        enterpriseRegistration_Region: [validators.regionValidator('', 3)],
        'enterpriseRegistrationProvince': [required('')],
        'enterpriseRegistrationCity': [required('')],
        'enterpriseRegistrationDistrict': [required('/')],
        'enterpriseRegistrationAddress': [validators.addressValidator('', true)],
        enterpriseManagement_Region: [validators.regionValidator('', 3)],
        'enterpriseManagementProvince': [required('')],
        'enterpriseManagementCity': [required('')],
        'enterpriseManagementDistrict': [required('/')],
        'enterpriseManagementAddress': [validators.addressValidator('', true)],
    },

}



