<template>
    <div>
        <Form ref="formModel" :model="formModel" :rules="guarantorEnterpriseRule" :label-width="120" v-if="formModel">
            <div>
                <h3 class="margin-b-14">
                    <ocr-button itemid="guarantorBusinessLicence" ocrtype="2" @on-ocr-result="setOcrRes">
                    </ocr-button>
                    <ocr-button itemid="guarantorIDPositive" ocrtype="1" @on-ocr-result="setOcrRes">
                        
                    </ocr-button>
                    <Button type="text" size="small" style="float: right;"
                            @click="deleteBondsman">
                    </Button>
                </h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorRelationship">
                            <i-select type="text" v-model.sync="formModel.guarantorRelationship" placeholder=""
                                      @on-change="val=>{ handleChange(val,'guarantorEnterprise.guarantorRelationship')}">
                                <i-option v-for="(item,index) in dp['relationship']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="enterpriseName">
                            <Input clearable v-model.sync="formModel.enterpriseName" placeholder=""
                                   v="guarantorEnterprise.enterpriseName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="enterpriseNature">
                            <i-select v-model.sync="formModel.enterpriseNature" placeholder=""
                                      @on-change="val=>{ handleChange(val,'guarantorEnterprise.enterpriseNature')}">
                                <i-option v-for="(item,index) in dp['firmNature']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>

                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="unifiedSocialCreditCode">
                            <Input clearable v-model.sync="formModel.unifiedSocialCreditCode" placeholder=""
                                   v="guarantorEnterprise.unifiedSocialCreditCode"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="corporationName">
                            <Input clearable v-model.sync="formModel.corporationName" placeholder=""
                                   v="guarantorEnterprise.corporationName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="corporationIdentityCard">
                            <Input clearable v-model.sync="formModel.corporationIdentityCard" placeholder=""
                                   v="guarantorEnterprise.corporationIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="corporationPhone">
                            <Input clearable v-model.sync="formModel.corporationPhone" placeholder=""
                                   v="guarantorEnterprise.corporationPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="actualControllerName">
                            <Input clearable v-model.sync="formModel.actualControllerName" placeholder=""
                                   v="guarantorEnterprise.actualControllerName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="actualControllerIdentityCard">
                            <Input clearable v-model.sync="formModel.actualControllerIdentityCard" placeholder=""
                                   v="guarantorEnterprise.actualControllerIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="actualControllerPhone">
                            <Input clearable v-model.sync="formModel.actualControllerPhone" placeholder=""
                                   v="guarantorEnterprise.actualControllerPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.enterpriseRegistration_Region"
                                           regionlabel="" ref="regionRef1"
                                           @on-change="changeRegion('enterpriseRegistration','guarantorEnterprise.enterpriseRegistration_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="enterpriseRegistrationAddress">
                            <Input clearable v-model.sync="formModel.enterpriseRegistrationAddress" placeholder=""
                                   v="guarantorEnterprise.enterpriseRegistrationAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.enterpriseManagement_Region"
                                           regionlabel="" ref="regionRef2"
                                           @on-change="changeRegion('enterpriseManagement','guarantorEnterprise.enterpriseManagement_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="enterpriseManagementAddress">
                            <Input clearable v-model.sync="formModel.enterpriseManagementAddress" placeholder=""
                                   v="guarantorEnterprise.enterpriseManagementAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 
     * @Props: {index: Number} 
     */
    import base from './js/custbase';
    import rules from './js/custRules';

    export default {
        name: 'orderGuarantorEnterpriseForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {
                guarantorEnterpriseRule: rules.guarantorEnterpriseRule, //
                targetObj: {},
            }
        },
        computed: {
            //model
            formModel() {
                let obj = this.orderData.cust.guarantorEnterprise[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.queryDictData(['relationship', 'firmNature']);
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //
            deleteBondsman() {
                this.$emit('on-delete', '2', this.index); //
            },
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //
                if (!this.checkFieldIsChange(handleField)) return false;
                if (handleField == 'guarantorEnterprise.0.enterpriseName') {
                    this.openEye(this.formModel.enterpriseName);
                }
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //
            handleChange(val, handleField, regionChange) {
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //store
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'guarantorEnterprise',
                    index: this.index,
                    field: field,
                })
                this.handleChange(field, handleField, true);
            },
            //
            validate(cb) {
                let refName = ['formModel', 'regionRef1', 'regionRef2'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    var flag = _.every(result);
                    cb && cb(flag);
                });
            },
            //
            setOcrRes(ocrRes) {
                if (ocrRes.flag == '2') {//
                    let hasChange = this.formModel.enterpriseName != ocrRes.enterpriseName ||
                        this.formModel.unifiedSocialCreditCode != ocrRes.cfuscc ||
                        this.formModel.enterpriseRegistrationAddress != ocrRes.residenceAddress;
                    if (!hasChange) {
                        this.openEye(ocrRes.enterpriseName)
                        return false;
                    }
                    this.formModel.enterpriseName = ocrRes.enterpriseName;//
                    this.formModel.unifiedSocialCreditCode = ocrRes.cfuscc;//
                    this.formModel.enterpriseRegistrationAddress = ocrRes.residenceAddress;//
                    if (ocrRes.enterpriseName) this.openEye(ocrRes.enterpriseName)
                } else {//
                    let hasChange = this.formModel.corporationIdentityCard != ocrRes.idcard ||
                        this.formModel.corporationName != ocrRes.name;
                    if (!hasChange) return false;
                    this.formModel.corporationName = ocrRes.name;//
                    this.formModel.corporationIdentityCard = ocrRes.idcard;//
                }
                this.$nextTick(() => {
                    this.cacheData('guarantorEnterprise')
                    this.todoRecount();
                })
            },
            //
            openEye(enterpriseName) {//
                if (!enterpriseName) return false;
                this.showLoading();
                this.$http.get(polarisserver + '/customer/openEye' + '?enterpriseName=' + enterpriseName, {}).then((res) => {
                    this.hideLoading();
                    let result = res.data;
                    if (result.success && !!result.data) {
                        let _data = result.data;
                        let eyeRes = {
                            enterpriseName: _data.enterpriseName ? _data.enterpriseName : this.formModel.enterpriseName,//
                            unifiedSocialCreditCode: _data.unifiedSocialCreditCode ? _data.unifiedSocialCreditCode : this.formModel.unifiedSocialCreditCode,//
                            enterpriseRegistrationAddress: _data.enterpriseRegistrationAddress ? _data.enterpriseRegistrationAddress : this.formModel.enterpriseRegistrationAddress,//
                            corporationName: _data.corporationName ? _data.corporationName : this.formModel.corporationName,//
                            corporationIdentityCard: _data.corporationIdentityCard ? _data.corporationIdentityCard : this.formModel.corporationIdentityCard,//
                        }
                        let hasChange = this.formModel.enterpriseName != eyeRes.enterpriseName ||
                            this.formModel.unifiedSocialCreditCode != eyeRes.unifiedSocialCreditCode ||
                            this.formModel.enterpriseRegistrationAddress != eyeRes.enterpriseRegistrationAddress ||
                            this.formModel.corporationName != eyeRes.corporationName ||
                            this.formModel.corporationIdentityCard != eyeRes.corporationIdentityCard;
                        if (!hasChange) return;
                        this.formModel.enterpriseName = eyeRes.enterpriseName;//
                        this.formModel.unifiedSocialCreditCode = eyeRes.unifiedSocialCreditCode;//
                        this.formModel.enterpriseRegistrationAddress = eyeRes.enterpriseRegistrationAddress;//
                        this.formModel.corporationName = eyeRes.corporationName;//
                        this.formModel.corporationIdentityCard = eyeRes.corporationIdentityCard;//
                        this.$nextTick(() => {
                            this.cacheData('guarantorEnterprise')
                            this.todoRecount();
                        })
                    } else if (result.success) {
                        return this.$Message.error('');
                    } else if (!result.success) {
                        return this.$Message.error(result.errorMessage || '');
                    }
                }).catch((err) => {
                    this.hideLoading();
                    this.errorCatchModal('', err);
                })
            },
        },
    }
</script>



