<template>
    <div>
        <Form ref="formModel" :model="formModel" :rules="guarantorPersonalRule" :label-width="120" v-if="formModel">
            <div>
                <h3 class="margin-b-14">
                    <ocr-button itemid="guarantorIDPositive" ocrtype="1" @on-ocr-result="setOcrRes">
                    </ocr-button>
                    <Button type="text" size="small" style="float: right;"
                            @click="deleteBondsman">
                    </Button>
                </h3>
                <Row>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorRelationship">
                            <i-select type="text" v-model.sync="formModel.guarantorRelationship" placeholder=""
                                      @on-change="val=>{ handleChange(val,'guarantorPersonal.guarantorRelationship')}">
                                <i-option v-for="(item,index) in dp['relationship']" :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorName">
                            <Input type="text" clearable
                                   v-model.sync="formModel.guarantorName" placeholder=""
                                   v="guarantorPersonal.guarantorName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorPhone">
                            <Input clearable
                                   v-model.sync="formModel.guarantorPhone" placeholder=""
                                   v="guarantorPersonal.guarantorPhone"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorCredentialType">
                            <i-select type="text" disabled
                                      v-model.sync="formModel.guarantorCredentialType" placeholder=""
                                      @on-change="val=>{ handleChange(val,'guarantorPersonal.guarantorCredentialType')}">
                                <i-option v-for="(item,index) in DICT_gcertType"
                                          :value="item.dictCode"
                                          :key="item.dictCode">
                                    {{item.dictName}}
                                </i-option>
                            </i-select>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="guarantorIdentityCard">
                            <Input clearable
                                   v-model.sync="formModel.guarantorIdentityCard" placeholder=""
                                   v="guarantorPersonal.guarantorIdentityCard"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8">
                        <FormItem label="" prop="companyPhone">
                            <Input clearable
                                   v-model.sync="formModel.companyPhone" placeholder=""
                                   v="guarantorPersonal.companyPhone"
                                   @on-blur="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="companyName">
                            <Input clearable
                                   v-model.sync="formModel.companyName" placeholder=""
                                   v="guarantorPersonal.companyName"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.company_Region"
                                           regionlabel="" ref="regionRef1"
                                           @on-change="changeRegion('company','guarantorPersonal.company_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="companyDetailAddress">
                            <Input clearable
                                   v-model.sync="formModel.companyDetailAddress" placeholder=""
                                   v="guarantorPersonal.companyDetailAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.currentResidence_Region"
                                           regionlabel="" ref="regionRef2"
                                           @on-change="changeRegion('currentResidence','guarantorPersonal.currentResidence_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="currentResidenceAddress">
                            <Input clearable
                                   v-model.sync="formModel.currentResidenceAddress" placeholder=""
                                   v="guarantorPersonal.currentResidenceAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" class="hascol-form-item">
                            <region-picker type="3" v-model="formModel.censusRegister_Region"
                                           regionlabel="" ref="regionRef3"
                                           @on-change="changeRegion('censusRegister','guarantorPersonal.censusRegister_Region')"></region-picker>
                        </FormItem>
                    </i-col>
                </Row>
                <Row>
                    <i-col :xs="24" :sm="16">
                        <FormItem label="" prop="censusRegisterAddress">
                            <Input clearable
                                   v-model.sync="formModel.censusRegisterAddress" placeholder=""
                                   v="guarantorPersonal.censusRegisterAddress"
                                   @on-blur="handleBlur" @on-clear="handleBlur"></Input>
                        </FormItem>
                    </i-col>
                </Row>
            </div>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 
     * @Props: {index: Number} 
     */
    import base from './js/custbase';
    import rules from './js/custRules';
    import {validators, required} from "@/utils/validateRules";

    export default {
        name: 'orderGuarantorPersonalForm',
        mixins: [base],
        props: {
            index: {
                default: 0
            },
        },
        components: {},
        data() {
            return {
                guarantorPersonalRule: rules.guarantorPersonalRule, //
                targetObj: {},
            }
        },
        computed: {
            //model
            formModel() {
                let obj = this.orderData.cust.guarantorPersonal[this.index];
                if (!obj) return undefined;
                return obj;
            },
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {
            //
            createCertNoRule() {
                var rule = [required('')];
                if (this.formModel.guarantorCredentialType == '1' || this.formModel.guarantorCredentialType == '7') {
                    rule.push({
                        validator: validators.certNo,
                        trigger: 'change'
                    })
                }
                return rule;
            },
            //
            deleteBondsman() {
                this.$emit('on-delete', '1', this.index); //
            },
            //
            handleBlur(e) {
                let handleField = e.target.parentNode.getAttribute('v') || e.target.parentNode.parentNode.getAttribute('v');
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //
                if (!this.checkFieldIsChange(handleField)) return false;
                this.$nextTick(() => {
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },

            //
            handleChange(val, handleField, regionChange) {
                let items = handleField.split('.');
                items.splice(1, 0, this.index);
                handleField = items.join('.')
                //
                if (!this.checkFieldIsChange(handleField) && !regionChange) return false;
                this.$nextTick(() => {
                    if (handleField == "guarantorPersonal.guarantorCredentialType" && this.formModel.guarantorIdentityCard) {//
                        this.$refs['formModel'].validateField('guarantorIdentityCard');
                    }
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },
            //store
            changeRegion(field, handleField) {
                this.setChangeRegion({
                    module: 'guarantorPersonal',
                    index: this.index,
                    field: field,
                })
                this.handleChange(field, handleField, true);
            },
            //
            validate(cb) {
                let flag = true;
                let refName = ['formModel', 'regionRef1', 'regionRef2', 'regionRef3'];
                let arr = [];
                refName.forEach(ref => {
                    arr.push(new Promise((resolve) => {
                        if (this.$refs[ref]) this.$refs[ref].validate((valid) => {
                            resolve(valid);
                        });
                        else resolve(true)
                    }))
                })
                Promise.all(arr).then(function (result) {
                    flag = _.every(result);
                    cb && cb(flag);
                });
            },
            //
            setOcrRes(ocrRes) {
                var hasChange = this.formModel.guarantorIdentityCard != ocrRes.idcard ||
                    this.formModel.guarantorName != ocrRes.name ||
                    this.formModel.censusRegisterAddress != ocrRes.address;
                if (!hasChange) return false;
                this.formModel.guarantorIdentityCard = ocrRes.idcard;//
                this.formModel.guarantorName = ocrRes.name;//
                this.formModel.guarantorCredentialType = "1";//
                this.formModel.censusRegisterAddress = ocrRes.address || '';//
                this.$nextTick(() => {
                    this.cacheData('guarantorPersonal');
                    this.todoRecount();
                })
            },
        },
        watch: {}
    }
</script>



